@use 'abstracts' as *;

// the asterisk is used so I don't have to namespace. If I were to use "@use 'abstracts' as a" then the prefix would have to be a.$secondary-color

header {
  @include nav-bar;
  background-color: $secondary-color;

  .logo__img {
    @include nav-logo;
  }

  nav {
    .nav__links {
      list-style: none;
      li {
        display: inline-block;
        padding: 0 20px;
        a {
          transition: all 0.3s ease 0s;
          color: $base-text;
          text-decoration: none;
          font-size: 2rem;
          &:hover {
            color: $hover-color;
          }
        }
      }
    }
  }
}
