@use 'abstracts' as *;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

footer {
  background-color: $secondary-color;
  height: auto;
  width: 100vw;
  padding-top: 40px;
  color: $base-text;
  .footer__content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;

    h3 {
      font-size: 2rem;
      font-weight: 400;
      text-transform: capitalize;
      line-height: 3rem;
    }
    p {
      max-width: 500px;
      margin: 10px auto;
      font-size: 14px;
    }
    .socials {
      list-style: none;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 1rem;

      li {
        margin: 0 20px;
      }

      a {
        text-decoration: none;
        color: $base-text;
        transition: all 0.3s ease 0s;

        &:hover {
          color: $hover-color;
        }
      }
    }
  }

  .footer__bottom {
    width: 100vw;
    padding: 20px 0;
    text-align: center;

    p {
      font-size: 14px;
      text-transform: capitalize;
    }
  }
}
