//Testing how to incorporate mixins

@mixin nav-logo {
  width: 70px;
  cursor: pointer;
  margin-right: auto;
}

@mixin nav-bar {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 10px 5%;
}

@mixin menu-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem;
}

@mixin menu-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #000066;
  font-size: 3rem;
  color: #b60000;
  height: 100%;
  width: 100%;
  border-radius: 20px;
  overflow: hidden;
}

@mixin menu-food {
  height: 300px;
}

@mixin menu-name {
  font-size: 4rem;
  margin-bottom: 1.5rem;
  text-align: center;
  font-family: "Work Sans", sans-serif;
  font-weight: 800;
}

@mixin menu-price {
  font-size: 2rem;
  margin-bottom: 2rem;
  font-family: "Work Sans", sans-serif;
}
